<template>
    <div class="container text-center pluginHome home-gallery">
        <h1 class="heading">v-uploader</h1>

        <p align="center">
            <iframe src="https://ghbtns.com/github-btn.html?user=TerryZ&repo=v-uploader&type=star&count=true" frameborder="0" scrolling="0" width="100px" height="20px"></iframe>
            <iframe src="https://ghbtns.com/github-btn.html?user=TerryZ&repo=v-uploader&type=fork&count=true" frameborder="0" scrolling="0" width="80px" height="20px"></iframe>
        </p>

        <p align="center" class="gallery-container">
            <v-gallery type="carousel" :images="list" ></v-gallery>
        </p>

        <p><i18n path="upload.projectDesc"></i18n></p>


        <p>
            <a href="https://www.npmjs.com/package/v-uploader" target="_blank">
                <img src="https://img.shields.io/npm/v/v-uploader.svg" alt="v-uploader-npm">
            </a>
            <a href="https://mit-license.org/" target="_blank">
                <img src="https://img.shields.io/badge/license-MIT-brightgreen.svg" alt="v-page-npm">
            </a>
        </p>
    </div>
</template>

<script>
    export default {
        data(){
            return {
                list: [
                    {
                        title: 'Single file upload with preview',
                        url: `${this.$store.state.baseImgUrl}/image/v-uploader/v-uploader-single.png`
                    },{
                        title: 'Multiple files upload with file drag',
                        url: `${this.$store.state.baseImgUrl}/image/v-uploader/v-uploader-multiple.png`
                    }
                ]
            };
        }
    }
</script>


